<template>
  <div class="detail">
    <div class="retbtn" @click="retClick">
      <span class="iconfont icon-zuo"></span>
    </div>
    <div class="det-img">
      <van-image :src="goodsInfo.img" width="100%" height="100%">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>加载失败</template>
      </van-image>
    </div>
    <div class="goodsdetail">
      <div class="goodsprice">
        ¥<span>{{ goodsInfo.price }}</span>
      </div>
      <div class="goodsname">{{ goodsInfo.name }}</div>
      <div class="goodsmsg"><div>快递：0.0</div></div>
    </div>
    <div class="select" @click="openShow">
      <span>选择</span>
      <div class="name">
        {{ goodsInfo.color ? goodsInfo.color : "" }}
        {{ goodsInfo.gg ? goodsInfo.gg : "" }}
      </div>
      <span class="iconfont icon-you"></span>
    </div>

    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
      ref="skuRef"
    />
    <van-goods-action>
      <van-goods-action-icon
        icon="cart-o"
        text="购物车"
        :badge="list.length"
        @click="gotoShoppingcar"
      />
      <van-goods-action-icon icon="star-o" text="收藏"/>
      <van-goods-action-button
        type="warning"
        text="加入购物车"
        @click="openShow"
      />
      <van-goods-action-button type="danger" text="立即购买" />
    </van-goods-action>
  </div>
</template>

<script>
import mixin from "@/mixin";
import { mapState, mapActions } from "vuex";
import detailModel from "@/model/detailModel";
import carModel from "@/model/carModel";
export default {
  mixins: [mixin],
  computed: {
    ...mapState(["userinfo"]),
    ...mapState("shoppingcar", ["list"]),
  },
  data() {
    return {
      goodsInfo: {},
      show: false,
      
      goods: {
        // 默认商品 sku 缩略图
        picture: "",
      },
      goodsId: 0,
      sku: {
        tree: [
          {
            k: "颜色", // skuKeyName：规格类目名称
            k_s: "s1", // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
            v: [
              {
                // id: 1, // skuValueId：规格值 id
                // name: "白色", // skuValueName：规格值名称
              },
            ],
            // largeImageMode: true, //  是否展示大图模式
          },
          {
            k: "规格", // skuKeyName：规格类目名称
            k_s: "s2", // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
            v: [
              {
                // id: 1, // skuValueId：规格值 id
                // name: "默认", // skuValueName：规格值名称
              },
            ],
            // largeImageMode: true, //  是否展示大图模式
          },
        ],
        // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
        list: [
          {
            // id: 1, // skuId
            // s1: 1, // 规格类目 k_s 为 s1 的对应规格值 id
            // s2: 1, // 规格类目 k_s 为 s2 的对应规格值 id
            // price: 100, // 价格（单位分）
            // stock_num: 100, // 当前 sku 组合对应的库存
          },
        ],
        price: "1.00", // 默认价格（单位元）
        stock_num: 1000, // 商品总库存
        hide_stock: true, // 是否隐藏剩余库存
      },
    };
  },
  async created() {
    let { data: list } = await detailModel.querySearch({
      goodsId: this.$route.params.id,
    });
    this.goodsInfo = list[0];
  },
  methods: {
    ...mapActions("shoppingcar", ["updateCarList", "addCarList"]),
    retClick() {
      this.$router.go(-1);
    },
    onBuyClicked() {},
    async onAddCartClicked(info) {
      if(!this.$store.state.userinfo.id){
        this.$router.push("/login")
        return false
      }
      let colorid = info.selectedSkuComb.s1;

      let ggid = info.selectedSkuComb.s2;

      let num = info.selectedNum;
      let goodsid = this.goodsInfo.goodsId;
      let userid = this.userinfo.id;
      let { data: list } = await carModel.query({
        userid,
        goodsid,
        ggid,
        colorid,
      });
      if (list.length) {
        this.updateCarList({
          num: list[0].num + num,
          carId: list[0].id,
        });
      } else {
        let colorname = this.sku.tree[0].v.filter(
          (item) => item.id == colorid
        )[0].name;
        let ggname = this.sku.tree[1].v.filter((item) => item.id == ggid)[0]
          .name;
        let goodsname = this.goodsInfo.name;
        let price = info.selectedSkuComb.price / 100;
        let img = this.goodsInfo.img;
        this.addCarList({
          goodsid,
          goodsname,
          userid,
          colorid,
          colorname,
          ggid,
          ggname,
          price,
          img,
          num,
        });
      }
      this.$toast.success("加入成功");
      this.show=false
      
    },

    async openShow() {
      this.show = true;
      if (this.goodsInfo.goodsId) {
        this.goodsId = this.goodsInfo.goodsId;
        let { data: list } = await detailModel.query({
          goodsid: this.goodsInfo.goodsId,
        });
        this.goods.picture = this.goodsInfo.img;
        if (list.length) {
          let colorIdList = Array.from(
            new Set(list.map((item) => item.colorid))
          );
          let colorList = colorIdList.map((id) => {
            let item = list.filter((item) => item.colorid == id)[0];
            return {
              id,
              name: item.colorname,
            };
          });
          this.sku.tree[0].v = colorList;

          let ggIdList = Array.from(new Set(list.map((item) => item.ggid)));
          let ggList = ggIdList.map((id) => {
            let item = list.filter((item) => item.ggid == id)[0];
            return {
              id,
              name: item.ggname,
            };
          });
          this.sku.tree[1].v = ggList;

          let allList = list.map((item) => {
            return {
              id: item.id,
              s1: item.colorid,
              s2: item.ggid,
              price: item.price * 100,
              stock_num: item.num,
            };
          });
          this.sku.list = allList;
        } else {
          this.goodsId = this.goodsInfo.goodsId;
          this.goods.picture = this.goodsInfo.img;
          if (this.goodsInfo.color) {
            this.sku.tree[0].v = [
              {
                id: 1,
                name: this.goodsInfo.color,
              },
            ];
          }
          if (this.goodsInfo.gg) {
            this.sku.tree[1].v = [
              {
                id: 1,
                name: this.goodsInfo.gg,
              },
            ];
          }
          this.sku.list[0] = {
            id: 1,
            s1: 1,
            s2: 1,
            price: this.goodsInfo.price * 100,
            stock_num: 100,
          };
          this.sku.price = this.goodsInfo.price;
        }
      } else {
        this.$router.push("/home");
      }
    },
    gotoShoppingcar() {
      if (this.$store.state.userinfo.id) {
        this.$router.push("/shoppingcar");
      } else {
        this.$toast("请先登录哦")
        this.$router.push("/login");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.detail {
  position: relative;
  .retbtn {
    z-index: 1;
    position: absolute;
    top: 15px;
    left: 15px;
    width: 30px;
    height: 30px;
    background: rgba($color: #999, $alpha: 0.8);
    text-align: center;
    color: #fff;
    line-height: 30px;
    font-size: 14px;
    border-radius: 15px;
    .icon-zuo {
      padding-left: 7px;
      font-size: 18px;
    }
  }
  .det-img {
    width: 100%;
    height: 375px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .goodsdetail {
    width: 100%;
    min-height: 112px;
    padding: 15px 15px 0 15px;
    background-color: #fff;
    box-sizing: border-box;
    .goodsprice {
      height: 22px;
      line-height: 22px;
      width: 100%;
      color: #e84040;
      font-size: 17px;
      span {
        font-size: 22px;
        margin-left: 5px;
      }
    }
    .goodsname {
      width: 100%;
      min-height: 48px;
      padding: 10px 0 14px 0;
      color: #363131;
      font-size: 17px;
      font-weight: bold;
      line-height: 24px;
      box-sizing: border-box;
    }
    .goodsmsg {
      width: 100%;
      height: 12px;
      padding-bottom: 15px;
      font-size: 12px;
      line-height: 12px;
      color: #a9aaab;
    }
  }
  .select {
    width: 100%;
    height: 50px;
    margin-top: 13px;
    padding: 0 12px;
    box-sizing: border-box;
    background: #fff;
    display: flex;
    align-items: center;
    span {
      font-size: 14px;
      color: #a9aaab;
      padding-right: 16px;
      line-height: 18px;
    }
    .name {
      font-size: 14px;
      color: #363131;
      line-height: 18px;
      flex: 1;
    }
    .icon-you {
      padding-right: 0;
    }
  }
}
</style>